<template>
  <h3>Computed 常見技巧 - 搜尋</h3>
  <input type="search" v-model="search" placeholder="輸入品項名稱">
  <input type="search" v-model="searchPrice" placeholder="輸入數字">
  <table class='table'>
    <tr v-for="item in filterProducts" :key='item'>
      <tb>{{ item.name }}</tb>
      <tb>{{ item.price }}</tb>
    </tr>
  </table>
</template>
<script>
export default {
  data () {
    return {
      num: 10,
      search: '',
      searchPrice: '',
      products: [
        {
          name: '蛋餅',
          price: 30,
          vegan: false
        },
        {
          name: '飯糰',
          price: 35,
          vegan: false
        },
        {
          name: '小籠包',
          price: 60,
          vegan: false
        },
        {
          name: '蘿蔔糕',
          price: 30,
          vegan: true
        }
      ],
      carts: [],
      sum: 0
    }
  },
  methods: {
    addToCart (product) {
      this.carts.push(product)
    }
  },
  computed: {
    total () {
      let total = 0
      this.carts.forEach(item => {
        total += item.price
      })
      return total
    },
    filterProducts () {
      return this.products
        .filter(item => {
          console.log(item)
          return item.name.match(this.search)
        })
    }
  },
  created () {
    console.log(this)
  }
}
</script>
